import React from 'react';

export const vusername = (value) => {
  var nospace = /^\S*$/;
  if (value.length === 0) return;

  if (value.length < 4 || value.length > 20) {
    return (
      <div className="alert alert-danger" role="alert">
        Prisijungimo vardas turi b큰ti sudarytas i큄 ne ma탑iau kaip 4 simboli킬.{' '}
      </div>
    );
  } else if (!value.match(nospace)) {
    return (
      <div className="alert alert-danger" role="alert">
        칼sitikinkite, kad 캄vesdami prisijungimo vard nepalikote tarp킬.{' '}
      </div>
    );
  }
};

export const vpassword = (value) => {
  var paswd = /^(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])[a-zA-Z0-9].{7,19}$/;
  if (value.length === 0) return;

  if (value.length < 8 || value.length > 20 || value.match(paswd) === null) {
    return (
      <div className="alert alert-danger" role="alert">
        Slapta탑odyje, kur캄 turi sudaryti i큄 820 simboli킬, turi b큰ti bent viena
        did탑ioji raid, viena ma탑oji raid ir vienas skaiius.
      </div>
    );
  }
};
